// ÂëΩ‰ª§Ôºö/airdrop_status
airdropBot.command('airdrop_status', async (ctx) => {
  try {
    // Ëé∑ÂèñÂΩìÂâçËΩÆÊ¨°‰ø°ÊÅØ
    const currentRound = await airdropContract.getCurrentRound();
    const poolConfig = await airdropContract.poolConfig();
    
    const now = Math.floor(Date.now() / 1000);
    const endTime = Number(currentRound.endTime);
    const remaining = endTime - now;
    
    const totalDeposits = parseFloat(ethers.formatEther(currentRound.totalDeposits));
    const weeklyReward = parseFloat(ethers.formatEther(poolConfig.weeklySmAmount));
    
    const statusMessage = `
üéØ **Á¨¨ ${currentRound.id} ËΩÆÁ©∫ÊäïÁä∂ÊÄÅ**

‚è∞ **Ââ©‰ΩôÊó∂Èó¥**: ${formatTimeRemaining(remaining)}
üí∞ **ÊÄªÊäïÂÖ•**: ${formatNumber(totalDeposits)} Â∞èÁ∫¢Ëä±
üë• **ÂèÇ‰∏é‰∫∫Êï∞**: ${currentRound.participantCount} ‰∫∫
üèÜ **Â•ñÂä±Ê±†**: ${formatNumber(weeklyReward)} SM

üìä **ËøõÂ∫¶**: ${((totalDeposits / (weeklyReward * 100)) * 100).toFixed(1)}%

${remaining > 0 ? 'üü¢ ÊäïÂÖ•ËøõË°å‰∏≠' : 'üî¥ Êú¨ËΩÆÂ∑≤ÁªìÊùü'}
`;

    await ctx.reply(statusMessage, {
      reply_markup: {
        inline_keyboard: [
          [{ text: 'üí∏ ÊäïÂÖ•Â∞èÁ∫¢Ëä±', callback_data: 'deposit_flowers' }],
          [{ text: 'üèÜ Êü•ÁúãÊéíË°åÊ¶ú', callback_data: 'leaderboard' }],
          [{ text: 'üîÑ Âà∑Êñ∞Áä∂ÊÄÅ', callback_data: 'airdrop_status' }]
        ]
      }
    });
    
  } catch (error) {
    console.error('Ëé∑ÂèñÁ©∫ÊäïÁä∂ÊÄÅÂ§±Ë¥•:', error);
    await ctx.reply('‚ùå Ëé∑ÂèñÁ©∫ÊäïÁä∂ÊÄÅÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï„ÄÇ');
  }
});

