// ÂëΩ‰ª§Ôºö/claim_reward
airdropBot.command('claim_reward', async (ctx) => {
  if (!ctx.session?.walletAddress) {
    await ctx.reply('‚ùå ËØ∑ÂÖàÂú® SocioMint ÁΩëÁ´ôÁªëÂÆöÊÇ®ÁöÑÈí±ÂåÖÂú∞ÂùÄ„ÄÇ');
    return;
  }
  
  try {
    // Êü•ËØ¢Áî®Êà∑ÂèØÈ¢ÜÂèñÁöÑÂ•ñÂä±
    const { data: deposits } = await supabase
      .from('airdrop_deposits')
      .select(`
        *,
        airdrop_rounds!inner(*)
      `)
      .eq('wallet_address', ctx.session.walletAddress)
      .eq('is_claimed', false)
      .eq('airdrop_rounds.is_distributed', true);
    
    if (!deposits || deposits.length === 0) {
      await ctx.reply('ü§∑‚Äç‚ôÇÔ∏è ÊöÇÊó†ÂèØÈ¢ÜÂèñÁöÑÂ•ñÂä±„ÄÇ');
      return;
    }
    
    let totalReward = 0;
    const rewardList = deposits.map(deposit => {
      const reward = parseFloat(deposit.actual_reward || '0');
      totalReward += reward;
      return `‚Ä¢ Á¨¨ ${deposit.round_id} ËΩÆ: ${formatNumber(reward)} SM`;
    }).join('\n');
    
    const claimUrl = 'https://sociomint.com/airdrop/claim?from=telegram';
    
    await ctx.reply(`
üèÜ **ÂèØÈ¢ÜÂèñÂ•ñÂä±**

${rewardList}

üí∞ **ÊÄªËÆ°**: ${formatNumber(totalReward)} SM

ËØ∑ÁÇπÂáª‰∏ãÊñπÊåâÈíÆÂâçÂæÄÁΩëÁ´ôÈ¢ÜÂèñÔºö
`, {
      reply_markup: {
        inline_keyboard: [
          [{ text: 'üèÜ ÂâçÂæÄÈ¢ÜÂèñ', url: claimUrl }],
          [{ text: 'üìä Êü•ÁúãÁªüËÆ°', callback_data: 'my_stats' }]
        ]
      }
    });
    
  } catch (error) {
    console.error('Êü•ËØ¢Â•ñÂä±Â§±Ë¥•:', error);
    await ctx.reply('‚ùå Êü•ËØ¢Â•ñÂä±Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï„ÄÇ');
  }
});

