// ÂëΩ‰ª§Ôºö/leaderboard
airdropBot.command('leaderboard', async (ctx) => {
  try {
    // Ëé∑ÂèñÂΩìÂâçËΩÆÊ¨°ÊéíË°åÊ¶ú
    const { data: leaderboard } = await supabase
      .from('round_leaderboard')
      .select('*')
      .order('rank', { ascending: true })
      .limit(10);
    
    if (!leaderboard || leaderboard.length === 0) {
      await ctx.reply('üìä ÊöÇÊó†ÊéíË°åÊ¶úÊï∞ÊçÆ„ÄÇ');
      return;
    }
    
    const leaderboardText = leaderboard.map((entry, index) => {
      const medal = index === 0 ? 'ü•á' : index === 1 ? 'ü•à' : index === 2 ? 'ü•â' : `${index + 1}.`;
      const username = entry.username || `${entry.wallet_address.slice(0, 6)}...${entry.wallet_address.slice(-4)}`;
      const amount = formatNumber(parseFloat(entry.deposited_flowers || '0'));
      const percentage = parseFloat(entry.deposit_percentage || '0').toFixed(1);
      
      return `${medal} ${username}: ${amount} (${percentage}%)`;
    }).join('\n');
    
    await ctx.reply(`
üèÜ **Êú¨ËΩÆÊéíË°åÊ¶ú Top 10**

${leaderboardText}

üí° ÊäïÂÖ•Êõ¥Â§öÂ∞èÁ∫¢Ëä±Ëé∑ÂæóÊõ¥È´òÊéíÂêçÔºÅ
`, {
      reply_markup: {
        inline_keyboard: [
          [{ text: 'üí∏ ÊäïÂÖ•Â∞èÁ∫¢Ëä±', callback_data: 'deposit_flowers' }],
          [{ text: 'üìä Êü•ÁúãÁä∂ÊÄÅ', callback_data: 'airdrop_status' }]
        ]
      }
    });
    
  } catch (error) {
    console.error('Êü•ËØ¢ÊéíË°åÊ¶úÂ§±Ë¥•:', error);
    await ctx.reply('‚ùå Êü•ËØ¢ÊéíË°åÊ¶úÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï„ÄÇ');
  }
});

// ÂõûË∞ÉÊü•ËØ¢Â§ÑÁêÜ
airdropBot.on('callback_query', async (ctx) => {
  const data = ctx.callbackQuery.data;
  
  switch (data) {
    case 'airdrop_status':
      await ctx.answerCbQuery();
      return ctx.scene.enter('airdrop_status');
    case 'deposit_flowers':
      await ctx.answerCbQuery();
      return ctx.reply('ËØ∑‰ΩøÁî®ÂëΩ‰ª§: /deposit_flowers [Êï∞Èáè]');
    case 'leaderboard':
      await ctx.answerCbQuery();
      return ctx.scene.enter('leaderboard');
    case 'my_stats':
      await ctx.answerCbQuery();
      return ctx.scene.enter('my_stats');
    case 'claim_reward':
      await ctx.answerCbQuery();
      return ctx.scene.enter('claim_reward');
    default:
      await ctx.answerCbQuery('Êú™Áü•Êìç‰Ωú');
  }
});

// ÂÆöÊó∂Êé®ÈÄÅÔºöÊØèÂë®ÊéíË°åÊ¶ú
export async function sendWeeklyLeaderboard() {
  try {
    // Ëé∑ÂèñÊâÄÊúâÁªëÂÆö Telegram ÁöÑÁî®Êà∑
    const { data: users } = await supabase
      .from('user_profiles')
      .select('telegram_username')
      .not('telegram_username', 'is', null);
    
    if (!users) return;
    
    // Ëé∑Âèñ‰∏äÂë®ÊéíË°åÊ¶ú
    const { data: leaderboard } = await supabase
      .from('round_leaderboard')
      .select('*')
      .order('rank', { ascending: true })
      .limit(5);
    
    if (!leaderboard || leaderboard.length === 0) return;
    
    const message = `
üèÜ **‰∏äÂë®Á©∫ÊäïÊéíË°åÊ¶ú**

${leaderboard.map((entry, index) => {
  const medal = index === 0 ? 'ü•á' : index === 1 ? 'ü•à' : index === 2 ? 'ü•â' : `${index + 1}.`;
  const username = entry.username || 'Anonymous';
  const amount = formatNumber(parseFloat(entry.deposited_flowers || '0'));
  return `${medal} ${username}: ${amount} Â∞èÁ∫¢Ëä±`;
}).join('\n')}

üéØ Êñ∞‰∏ÄËΩÆÁ©∫ÊäïÂ∑≤ÂºÄÂßãÔºåÂø´Êù•ÂèÇ‰∏éÂêßÔºÅ
`;

    // ÂèëÈÄÅÁªôÊâÄÊúâÁî®Êà∑ÔºàÂÆûÈôÖÂ∫îÁî®‰∏≠ÂèØËÉΩÈúÄË¶ÅÂàÜÊâπÂèëÈÄÅÔºâ
    for (const user of users.slice(0, 10)) { // ÈôêÂà∂ÂèëÈÄÅÊï∞Èáè
      try {
        await airdropBot.telegram.sendMessage(user.telegram_username, message);
        await new Promise(resolve => setTimeout(resolve, 100)); // ÈÅøÂÖçÈ¢ëÁéáÈôêÂà∂
      } catch (error) {
        console.error(`ÂèëÈÄÅÊ∂àÊÅØÁªô ${user.telegram_username} Â§±Ë¥•:`, error);
      }
    }
    
  } catch (error) {
    console.error('ÂèëÈÄÅÂë®ÊéíË°åÊ¶úÂ§±Ë¥•:', error);
  }
}

